.accordion {
}
.accordion__title {
  font-weight: 600;
  font-size: 4rem;
  line-height: 120%;
}
.accordion__box {  &:not(:last-child) {
    margin-bottom: 12px;
  }
}
.accordion__btn {
  cursor: pointer;
  display: block;
  width: 100%;
  height: 100%;
  text-align: left;
  border: none;

  font-weight: 600;
  font-size: 2rem;
  line-height: 120%;

  padding: 20px 24px;
  background-color: #fff;
  border-radius: 16px;
}
.accordion__content {
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.3s ease;
}
